.popup-model {
  position: fixed;
  width: 100%;
  background-color: $white;
  left: 0;
  top: 0;
  padding: 0 15px 15px 15px;
  opacity: 0;
  min-height: 318px;
  max-width: 100%;
  z-index: -1;
  visibility: hidden;
  transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
  @include screen(laptop) {
    padding: 40px;
    border-radius: 10px;
    max-width: calc(100% - 96px);
    max-height: calc(100dvh - 96px);
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
  }
  &.active {
    opacity: 1;
    z-index: 100;
    visibility: visible;
  }
  &__bg {
    position: fixed;
    transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(#222222, 0.8);
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    &.active {
      opacity: 1;
      z-index: 99;
      visibility: visible;
    }
  }
  &__close {
    position: absolute;
    right: 10px;
    display: none;
    top: 10px;
    cursor: pointer;
    background-color: #E8EBF1;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      display: flex;
      width: 60px;
      height: 60px;
      transition: background-color $transition-default;
    }
    svg {
      path {
        @include screen(laptop) {
          transition: fill $transition-default;
        }
      }
    }
    @include screen(laptop) {
      &:hover {
        background-color: $primary;
        svg {
          path {
            fill: $white;
          }
        }
      }
    }
  }
  &__back {
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    @include screen(laptop) {
      display: none;
    }
    &__icon {
      position: absolute;
      left: 0;
      width: 44px;
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    p {
      font-size: 14px;
      font-weight: 500;
      line-height: 17px;
      letter-spacing: -0.01em;
    }
  }
  &__container {
    padding-top: 30px;
    max-height: calc(100dvh - 15px - 52px);
    border-top: 1px solid #EDEEF0;
    overflow-y: auto;
    @include screen(laptop) {
      border-top: none;
      padding-top: 0;
      display: grid;
      gap: 32px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__gallery {
    margin-top: 20px;
    @include screen(laptop) {
    margin-top: 0;
      grid-column: span 6 / span 6;
    }
    @include screen(desktop) {
      grid-column: span 5 / span 5;
    }
    .swiper {
      width: 100%;
      border-radius: 10px;
      &-slide {
        img {
          width: 100%;
          object-fit: cover;
          border-radius: 10px;
          max-height: 400px;
          min-height: 400px;
          @include screen(desktop) {
            max-height: 380px;
            min-height: 380px;
          }
          @include screen(desktop) {
            max-height: 417px;
            min-height: 417px;
          }
        }
      }
      &-navigation {
        position: absolute;
        display: flex;
        align-items: center;
        gap: 8px;
        right: 16px;
        bottom: 16px;
      }
      &-button {
        &-prev,
        &-next {
          cursor: pointer;
          inset: auto;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: $white;
          border-radius: 50%;
          position: relative;
          width: 40px;
          transition: opacity $transition-default;
          height: 40px;
          margin: 0;
          @include screen(laptop) {
            height: 50px;
            width: 50px;
          }
          &:before,
          &:after {
            display: none;
          }
          svg {
            width: 18px;
            height: 18px;
            @include screen(laptop) {
              width: 24px;
              height: 24px;
            }
          }
        }
      }
    }
  }
  &__prices {
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: space-between;
    @include screen(laptop) {
      background-color: $gray;
      border-radius: 10px;
      margin-top: 8px;
      padding: 24px 27px;
    }
    h3 {
      font-size: 20px;
      font-weight: 500;
      line-height: 26px;
      letter-spacing: -0.01em;
      @include screen(laptop) {
        font-size: 22px;
      }
    }
  }
  &__buttons {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 20px;
    gap: 8px;
    @include screen(laptop) {
      flex-direction: row;
      gap: 14px;
      margin-top: 22px;
      align-items: center;
    }
  }
  &__button {
    max-height: 54px;
    height: 54px;
    @include screen(laptop) {
      max-height: 74px;
      height: 74px;
    }
    span {
      padding: 27px 24px;
    }
    .button__icon {
      height: 100%;
    }
    &-white {
      border: 1px solid $primary;
      background-color: transparent;
      span {
        color: $primary;
      }
      .button__icon {
        border-color: $primary;
        svg {
          path {
            fill: $primary;
          }
        }
      }
    }
  }
  &__price {
    font-size: 20px;
    line-height: 26px;
    letter-spacing: -0.01em;
    font-weight: 500;
    display: flex;
    align-items: center;
    font-variant-numeric: lining-nums;
    gap: 4px;
    @include screen(laptop) {
      font-size: 22px;
    }
  }
  &__inner {
    @include screen(laptop) {
      padding-right: 20px;
      max-height: calc(100dvh - 96px - 96px);
      overflow-y: auto;
      overflow-x: hidden;
    }
  }
  &__title {
    font-size: 32px;
    font-weight: 500;
    line-height: 35px;
    text-transform: uppercase;
    @include screen(laptop) {
      display: none;
    }
  }
  &__content {
    margin-top: 20px;
    @include screen(laptop) {
      padding-right: 40px;
      margin-top: 0;
      grid-column: span 6 / span 6;
    }
    @include screen(desktop) {
      grid-column: span 7 / span 7;
    }
    h2 {
      display: none;
      @include screen(laptop) {
        line-height: 43px;
        font-size: 36px;
      }
    }
    p {
      font-weight: 500;
      font-size: 16px;
      line-height: 22px;
      margin-top: 16px;
      letter-spacing: -0.01em;
      @include screen(laptop) {
        font-size: 18px;
        line-height: 25px;
      }
    }
    table {
      margin-top: 23px;
      @include screen(laptop) {
        margin-top: 26px;
      }
      tr {
        padding: 16px 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
        border-bottom: 1px solid rgba($dark, 0.3);
        @include screen(laptop) {
          display: grid;
          gap: 0;
          padding: 20px 0;
          grid-template-columns: repeat(2, minmax(0, 1fr));
        }
        &:last-child {
          border-bottom: none;
        }
        td {
          font-size: 16px;
          font-weight: 500;
          line-height: 22px;
          letter-spacing: -0.01em;
          text-align: left;
          @include screen(laptop) {
            font-size: 18px;
            line-height: 25px;
          }
          img {
            max-width: 327px;
          }
        }
      }
    }
  }
}