.about {
  padding: 30px 0 926px;
  @include screen(laptop) {
    padding: 60px 0 314px;
  }
  &__heading {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 16px;
    h2 {
      @include screen(laptop) {
        grid-column: span 3 / span 3;
      }
    }
    p {
      font-weight: 500;
      font-size: 22px;
      line-height: 26px;
      letter-spacing: -0.01em;
      br {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
      @include screen(laptop) {
        font-size: 28px;
        line-height: 33px;
        grid-column: span 5 / span 5;
      }
    }
    span {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: -0.01em;
      @include screen(laptop) {
        grid-column: span 4 / span 4;
      }
    }
    @include screen(laptop) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__controls {
    margin-top: 20px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 16px;
    @include screen(laptop) {
      display: grid;
      margin-top: 56px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 32px;
    }
  }
  &__choose {
    position: relative;
    width: 100%;
    @include screen(laptop) {
      grid-column: span 3 / span 3;
      width: auto;
    }
  }
  &__current {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 14px 19px 14px 29px;
    border-radius: 8px;
    text-align: left;
    width: 100%;
    border: 1px solid #D1D1D1;
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.01em;
    svg {
      transition: transform $transition-default;
    }
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
    @include screen(laptop) {
      padding: 24px 26px 24px 29px;
    }
  }
  &__switcher {
    display: flex;
    align-items: center;
    gap: 4px;
    width: 100%;
    @include screen(laptop) {
      width: auto;
      border: 1px solid #D1D1D1;
      padding: 8px;
      border-radius: 8px;
      grid-column: span 5 / span 5;
    }
  }
  &__switch {
    font-size: 16px;
    font-weight: 500;
    justify-content: center;
    padding: 16px;
    width: 100%;
    line-height: 22px;
    background-color: #F2F2F2;
    letter-spacing: -0.01em;
    &.active {
      background-color: $primary;
      color: $white;
      @include screen(laptop) {
        &:hover {
          background-color: $primary-hover;
        }
      }
    }
    @include screen(laptop) {
      padding: 17px;
      background-color: transparent;
      width: auto;
      padding: 17px 106px;
    }
  }
  &__list {
    position: absolute;
    top: 80px;
    padding: 9px;
    border: 1px solid #D1D1D1;
    border-radius: 8px;
    width: 100%;
    background-color: $white;
    max-height: 274px;
    display: none;
    overflow-y: auto;
    &.active {
      display: block;
      z-index: 10;
    }
  }
  &__city {
    padding: 12px;
    width: 100%;
    font-size: 16px;
    margin-top: 6px;
    text-align: left;
    border-radius: 8px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.01em;
    transition: color $transition-default, background-color $transition-default;
    &:first-child {
      margin-top: 0;
    }
    @include screen(laptop) {
      &:hover {
        background-color: #F2F2F2;
      }
    }
    &.active {
      background-color: $primary;
      color: $white;
      &:hover {
        background-color: $primary-hover;
      }
    }
  }
  &__containers {
    margin-top: 30px;
    position: relative;
  }
  &__container {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    min-height: 400px;
    width: 100%;
    @include screen(laptop) {
      border-radius: 10px;
      overflow: hidden;
    }
    &.active {
      opacity: 1;
    }
    @include screen(laptop) {
      max-height: 700px;
      min-height: 700px;
    }
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    ymaps,
    iframe {
      border-radius: 10px;
      overflow: hidden;
    }
  }
  &__search {
    background-color: rgba($white, 0.5);
    border: 1px solid #D1D1D1;
    display: flex;
    align-items: center;
    overflow: hidden;
    padding: 24px 27px;
    max-width: 638px;
    border-radius: 8px;
  }
  &__input {
    font-family: Raleway;
    padding: 0 15px;
    font-size: 16px;
    width: 100%;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: -0.01em;
    text-align: left;
    background-color: transparent;
  }
  &__icon {
    svg {
      width: 24px;
      height: 24px;
    }
  }
  &__inner {
    height: 100%;
    background-color: transparent;
    @include screen(laptop) {
      padding: 50px;
      background-color: $gray;
    }
  }
  &__items {
    margin-top: 30px;
    max-height: 1000px;
    padding: 0 16px;
    border-radius: 10px;
    overflow-y: auto;
    background-color: $gray;
    @include screen(laptop) {
      max-height: 500px;
      padding: 0;
      background-color: transparent;
    }
  }
  &__item {
    padding: 20px 0;
    border-bottom: 1px solid rgba($dark, 0.2);
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    @include screen(laptop) {
      padding: 24px 0;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      gap: 32px;
    }
    &:last-child {
      border-bottom: none;
    }
  }
  &__col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    &-1 {
      grid-column: span 1 / span 1;
    }
    &-2 {
      grid-column: span 2 / span 2;
    }
    &-3 {
      grid-column: span 3 / span 3;
    }
    &-4 {
      grid-column: span 4 / span 4;
    }
    &-right {
      @include screen(laptop) {
        align-items: flex-end;
      }
    }
    p {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: -0.01em;
    }
    a {
      font-size: 16px;
      font-weight: 500;
      line-height: 22px;
      letter-spacing: -0.01em;
      @include screen(laptop) {
        transition: color $transition-default, border-color $transition-default;
        &:hover {
          color: $primary;
          border-color: $primary;
        }
      }
    }
  }
  &__link {
    color: $primary;
    border-bottom: 1px solid transparent;
  }
}