.popup-sign-up {
  position: fixed;
  width: 100%;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  background-color: $white;
  border-radius: 10px;
  opacity: 0;
  min-height: 318px;
  max-width: 384px;
  padding: 70px 16px 18px;
  z-index: -1;
  visibility: hidden;
  transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
  @include screen(laptop) {
    padding: 100px 128px;
    max-width: 990px;
  }
  &.active {
    opacity: 1;
    z-index: 100;
    visibility: visible;
  }
  &__bg {
    position: fixed;
    transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(#222222, 0.8);
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    &.active {
      opacity: 1;
      z-index: 99;
      visibility: visible;
    }
  }
  &__close {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 50px;
    cursor: pointer;
    height: 50px;
    background-color: #E8EBF1;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      width: 60px;
      height: 60px;
      transition: background-color $transition-default;
    }
    svg {
      path {
        @include screen(laptop) {
          transition: fill $transition-default;
        }
      }
    }
    @include screen(laptop) {
      &:hover {
        background-color: $primary;
        svg {
          path {
            fill: $white;
          }
        }
      }
    }
  }
  &__title {
    font-size: 30px;
    font-weight: 500;
    line-height: 30px;
    text-transform: uppercase;
    br {
      @include screen(laptop) {
        display: none;
      }
    }
    @include screen(laptop) {
      font-size: 50px;
      line-height: 50px;
      text-align: center;
    }
  }
  &__text {
    font-weight: 500;
    margin-top: 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    @include screen(laptop) {
      margin-top: 18px;
      font-size: 16px;
      line-height: 20px;
      text-align: center;
    }
  }
  &__form {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    display: grid;
    gap: 8px;
    margin-top: 23px;
    @include screen(laptop) {
      grid-template-columns: repeat(12, minmax(0, 1fr));
      row-gap: 40px;
      column-gap: 32px;
      margin-top: 40px;
    }
  }
  &__label {
    grid-column: span 1 / span 1;
    @include screen(laptop) {
      grid-column: span 6 / span 6;
    }
  }
  &__input {
    border: 1px solid rgba($dark, 0.4);
    font-size: 16px;
    font-weight: 500;
    padding: 16px 29px;
    border-radius: 6px;
    line-height: 22px;
    font-variant-numeric: lining-nums;
    background-color: transparent;
    width: 100%;
    letter-spacing: -0.01em;
    transition: border-color $transition-default;
    &.not-valid {
      border-color: red;
    }
    @include screen(laptop) {
      font-size: 16px;
      font-weight: 500;
      padding: 26px 29px;
      line-height: 22px;
      letter-spacing: -0.01em;
    }
  }
  &__success {
    display: none;
  }
  &__button {
    max-width: 352px;
    margin: 69px auto 0;
    @include screen(laptop) {
      margin-top: 0;
      grid-column: span 6 / span 6;
      grid-column-start: 4;
    }
    &-success {
      margin-top: 40px;
    }
  }
  &__policy {
    font-size: 12px;
    font-weight: 500;
    max-width: 300px;
    line-height: 16px;
    margin-top: 10px;
    @include screen(laptop) {
      max-width: 468px;
      margin: 16px auto 0;
      text-align: center;
    }
    a {
      border-bottom: 1px solid $dark;
      @include screen(laptop) {
        transition: border-color $transition-default, color $transition-default;
        &:hover {
          border-color: $primary;
          color: $primary;
        }
      }
    }
  }
}