.footer {
  padding: 240px 0 0;
  background-color: $dark;
  @include screen(laptop) {
    padding: 400px 0 0;
  }
  &__menu {
    padding: 33px 0 79px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 79px;
    @include screen(laptop) {
      flex-direction: row;
      padding: 33px 0;
      gap: 0;
    }
  }
  &__left,
  &__right {
    display: flex;
    align-items: center;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
    }
  }
  &__left {
    gap: 79px;
    @include screen(laptop) {
      gap: 87px;
    }
  }
  &__nav {
    display: flex;
    gap: 40px;
    align-items: center;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
      gap: 50px;
    }
  }
  &__link {
    color: $white;
    font-size: 20px;
    line-height: 22px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: left;
    font-variant-numeric: lining-nums;
    @include screen(laptop) {
      font-size: 14px;
      line-height: 20px;
      transition: color $transition-default;
      &:hover {
        color: $primary;
      }
    }
  }
  &__copy {
    padding: 30px 0;
    border-top: 1px solid rgba($white, 0.3);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column-reverse;
    gap: 30px;
    @include screen(laptop) {
      flex-direction: row;
    }
    p {
      text-transform: uppercase;
      color: rgba($white, 0.3);
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;
      font-variant-numeric: lining-nums;
    }
  }
  &__icons {
    display: flex;
    gap: 8px;
    align-items: center;
  }
  &__icon {
    width: 54px;
    height: 54px;
    border: 1px solid rgba($white, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $primary;
      }
    }
    svg {
      width: 18px;
      height: 18px;
    }
  }
}