.gallery {
  padding: 30px 0;
  overflow: hidden;
  @include screen(laptop) {
    padding: 60px 0;
  }
  &__buttons {
    display: inline-flex;
    margin-top: 30px;
    width: 100%;
    overflow-x: auto;
    align-items: flex-start;
    gap: 4px;
    @include screen(laptop) {
      margin-top: 48px;
      background-color: $white;
      width: auto;
      overflow: hidden;
      border: 1px solid #E5E5E5;
      max-width: 100%;
      border-radius: 10px;
      padding: 9px;
    }
  }
  &__button {
    padding: 17px;
    border-radius: 4px;
    text-align: center;
    font-size: 16px;
    white-space: nowrap;
    background-color: #F2F2F2;
    line-height: 19px;
    font-weight: 500;
    width: 100%;
    transition: color $transition-default, background-color $transition-default;
    &.active {
      color: $white;
      background-color: $primary;
    }
    &:hover {
      color: $white;
      background-color: $primary;
    }
    @include screen(laptop) {
      background-color: transparent;
      font-size: 16px;
      text-align: left;
      line-height: 22px;
    }
  }
  &__sliders {
    margin-top: 16px;
    @include screen(laptop) {
      margin-top: 24px;
    }
  }
  &__slider {
    display: none;
    position: relative;
    padding: 0 0 40px 0;
    &.active {
      display: block;
    }
    .swiper-gallery {
      overflow: visible;
    }
    .swiper-slide {
      min-height: 369px;
      max-height: 369px;
      @include screen(laptop) {
        min-height: 700px;
        max-height: 700px;
      }
    }
    .swiper-scrollbar {
      display: none;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      &-drag {
        background-color: $primary;
      }
      @include screen(laptop) {
        display: block;
      }
    }
    .swiper-navigation {
      position: absolute;
      display: flex;
      align-items: center;
      gap: 8px;
      top: 10px;
      right: 10px;
      @include screen(laptop) {
        right: 30px;
        bottom: 30px;
        top: auto;
      }
    }
    .swiper-button {
      &-prev,
      &-next {
        cursor: pointer;
        inset: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $white;
        border-radius: 50%;
        position: relative;
        transition: opacity $transition-default;
        width: 40px;
        height: 40px;
        margin: 0;
        @include screen(laptop) {
          height: 74px;
          width: 74px;
        }
        &:before,
        &:after {
          display: none;
        }
        svg {
          width: 18px;
          height: 18px;
          @include screen(laptop) {
            width: 24px;
            height: 24px;
          }
        }
      }
    }
  }
  &__slide {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 100%;
  }
  &__content {
    padding: 8px;
    position: absolute;
    @include screen(laptop) {
      padding: 30px;
    }
  }
  &__image {
    border-radius: 10px;
    width: 100%;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      min-height: 369px;
      max-height: 369px;
      @include screen(laptop) {
        min-height: 700px;
        max-height: 700px;
      }
    }
  }
  &__more {
    span {
      padding-right: 84px;
    }
  }
}